import React, { useState } from 'react';
import styled from 'styled-components';

import { TagsMap, TagsArray } from '@t/components';
import { WEEKLY_ALL_TAG, weeklyPickUrl } from '@/constant';
import { InlineBlockLink } from '@/components/common/Link';
import createGA from '@/components/common/createGA';

interface WeeklyPickTagsProps {
  isShowCnt?: boolean;
  currentTag?: string;
  tags: TagsMap | TagsArray;
  lang: 'ko' | 'en';
}

interface TagWithCountProps {
  currentTag?: string;
  tags: TagsMap;
  lang: 'ko' | 'en';
}

interface TagProps {
  tags: TagsArray;
  lang: 'ko' | 'en';
}

const Tags = styled.div`
  & a {
    margin-right: 10px;
  }

  &.showCnt a {
    margin-top: 12px;
  }

  @media (max-width: 720px) {
    &.more {
      max-height: 120px;
      overflow: hidden;
    }

    &.open {
      max-height: none;
    }

    & a {
      margin-top: 5px;
    }

    &.showCnt a {
      margin-top: 10px;
    }
  }
`;

const Tag = styled.div`
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 14px;
  color: #222;

  &:hover {
    border: 1px solid #b0b6ff;
    background-color: #edeeff;
  }

  &.selected {
    background-color: #515ce6;

    & span {
      color: #fff;
    }
  }

  @media (max-width: 720px) {
    height: 25px;
    line-height: 25px;
    font-size: 12px;
  }
`;

const TagName = styled.span`
  color: #222;
  margin-right: 4px;
`;

const TagCount = styled.span`
  color: #aaa;
`;

const MobileMoreButtonWrapper = styled.div`
  display: none;

  @media (max-width: 720px) {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 17px;
    font-size: 14px;
    color: #515ce6;
    font-weight: 500;
    cursor: pointer;
  }
`;

// TODO : 스프라이트 이미지로 변경 예정
const MoreIcon = styled.i`
  @media (max-width: 720px) {
    width: 16px;
    height: 16px;
    margin-left: 3px;

    &.more {
      background: url(https://uicdn.toast.com/toastui/img/toast-ui-mobile-btn-more-open.png) center;
    }

    &.close {
      background: url(https://uicdn.toast.com/toastui/img/toast-ui-mobile-btn-more-close.png) center;
    }
  }
`;

const getTagsWithCntComponent = ({ currentTag, tags, lang }: TagWithCountProps) => {
  const prefix = weeklyPickUrl[lang.toUpperCase() as 'KO' | 'EN'];
  const ga = createGA('Weekly Pick - main', 'click', 'weekly tag');

  return Object.keys(tags).map((tag) => {
    const url = tag === WEEKLY_ALL_TAG ? `${prefix}` : `${prefix}/${tag}`;
    const selected =
      currentTag === tag || (!currentTag && tag === WEEKLY_ALL_TAG) ? 'selected' : '';

    return (
      <InlineBlockLink
        key={tag}
        type="internal"
        url={url}
        testId="tags"
        ga={{ ...ga, label: `weekly tag - ${tag}` }}
      >
        <Tag className={selected}>
          <TagName>{`#${tag}`}</TagName>
          <TagCount>{tags[tag]}</TagCount>
        </Tag>
      </InlineBlockLink>
    );
  });
};

const getTagsComponent = ({ tags, lang }: TagProps) => {
  const prefix = `${weeklyPickUrl[lang.toUpperCase() as 'KO' | 'EN']}`;
  const ga = createGA('Weekly Pick - main', 'click', 'weekly tag');

  return tags.map((tag) => (
    <InlineBlockLink
      key={tag}
      type="internal"
      url={`${prefix}/${tag}`}
      testId="tags"
      ga={{ ...ga, label: `weekly tag - ${tag}` }}
    >
      <Tag className="tag">
        <TagName>{`#${tag}`}</TagName>
      </Tag>
    </InlineBlockLink>
  ));
};

const WeeklyPickTags: React.FC<WeeklyPickTagsProps> = ({ currentTag, tags, lang, isShowCnt }) => {
  const [isMobileMore, setMobileMore] = useState<'more' | 'close'>(!currentTag ? 'more' : 'close');
  const cntClassName = isShowCnt ? 'showCnt' : '';

  return (
    <>
      <Tags className={`${cntClassName} ${isMobileMore}`}>
        {isShowCnt
          ? getTagsWithCntComponent({ currentTag, tags, lang } as TagWithCountProps)
          : getTagsComponent({ lang, tags } as TagProps)}
      </Tags>
      {isShowCnt ? (
        <MobileMoreButtonWrapper
          onClick={() => setMobileMore(isMobileMore === 'more' ? 'close' : 'more')}
        >
          {isMobileMore}
          <MoreIcon className={isMobileMore} />
        </MobileMoreButtonWrapper>
      ) : null}
    </>
  );
};

export default WeeklyPickTags;
